.backgroundLogin {
  height: 90vh;
}
.curvesLogin {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.loginForm {
  position: absolute;
  background: white;
  border: 1px solid rgb(222, 222, 222);
  width: 340px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0 14px 18px 14px;
  text-align: center;
  border-radius: 8px;
  box-shadow: 3px 3px 10px grey;
  animation: glowing 3s linear infinite;
}
.input {
  display: block;
  min-width: 100%;
  margin: 0 0 25px 0;
}
.loginTitle {
  margin: 0 0 30px 0;
  padding: 15px 0 25px 0;
}
.loginBtn {
  width: 100%;
  margin: 7px 0 0 0;
}
.curvessLoginUpper {
  transform: rotate(180deg);
  position: absolute;
  top: 0;
  z-index: -1;
}
@media only screen and (max-width: 900px) {
  .curvessLoginUpper,
  .curvesLogin {
    display: none;
  }
}

@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    border-radius: 0;
  }
  100% {
    transform: translateY(-1000px) rotate(720deg);
    opacity: 0;
    border-radius: 50%;
  }
}

.background {
  position: fixed;
  z-index: -1;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  background: #ffd9a8;
  overflow: hidden;
}
.background li {
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.2);
  animation: animate 19s linear infinite;
}

